@-webkit-keyframes z4 {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes z4 {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.sp__wrapper {
  -webkit-animation: z4 1400ms linear infinite;
          animation: z4 1400ms linear infinite;
}
